import React from "react"
import ListItem from "./ListItem"
import Add from "./Add"
import Edit from "./Edit"
import Search from "./Search"
import notesArray from "./notesArray"
import "./style.css"

class App extends React.Component {
  state = {
    notes: [...notesArray],
    isEditOpen: false,
    id: "",
  }

  addItem = (e) => {
    e.preventDefault()
    const newItem = {
      id: String(e.target[0].value.concat(notesArray.length + 1)),
      title: e.target[0].value,
      content: e.target[1].value,
    }

    this.setState(prevState => ({
      notes: [...prevState.notes, newItem]
    }))
    notesArray.push(newItem)
    e.target.reset()
  }

  removeItem = (id) => {
    for (let i = 0; i < notesArray.length; i++) {
      if (notesArray[i].id === id) {
        notesArray.splice(i, 1)
      }
    }
    this.setState({ notes: [...notesArray] })
  }

  editItem = (e, id, titleVal, contentVal) => {
    e.preventDefault()
    for (let i = 0; i < notesArray.length; i++) {
      if (notesArray[i].id === id) {
        notesArray[i].title = titleVal
        notesArray[i].content = contentVal
      }
    }
    this.setState({ notes: [...notesArray] })
    e.target.reset()
  }

  closeEdit = () => {
    this.setState({
      isEditOpen: false,
    })
  }

  openEdit = (id) => {
    this.setState({
      isEditOpen: true,
      id: id
    })
  }

  searchFunction = (foundList) => {
    this.setState({
      notes: foundList
    })
  }

  render() {
    const { isEditOpen } = this.state
    return (
      <>
        <header>
          <h2>_notemacher</h2>
          <Search restoreFunction={this.restoreFunction} searchFunction={this.searchFunction} />
        </header>
        <main>
          <article className="listNotes">
            <h3>Notes</h3>
            {this.state.notes.map(note => (
              <ListItem key={note.id} {...note} open={this.openEdit} deleteIt={this.removeItem} />
            ))}
            <hr />
          </article>
          {isEditOpen && <Edit close={this.closeEdit} id={this.state.id} changeFunction={this.editItem} edit />}
          <aside>
            <Add addFunction={this.addItem} />
          </aside>
        </main>
        <footer><p>_notemacher: React 17 + hooks</p></footer>
      </>
    )
  }
}

export default App 